import React, { useState, useEffect } from 'react';
import { searchBook } from './api/searchBook';
const formStyle = {
  padding: 20
};
const inputStyle = {
  margin: 10
};

export const Search = () => {
  const [search, setSearch] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const updateSearch = e => {
    setSearch(e.target.value);
  };

  const searchBook = e => {
    console.log(e);
    e.preventDefault();
    console.log(search);
    searchBook(search);
  };

  return (
    <form style={formStyle}>
      <input
        onInput={updateSearch}
        className="form-control"
        style={inputStyle}
        placeholder="Search"
      />
      <button onSubmit={searchBook} style={inputStyle} className="btn btn-dark">
        Search
      </button>
    </form>
  );
};
